import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import "./print.css";

import IntlMessages from "../../components/utility/intlMessages";
import { siteConfig } from "../../settings";

import { Row, Col, Divider } from "antd";
import PrintTable from "../Tables/antTables/PrintTable.style";
import { Text, Title, MiniText } from "./app.style";
import moment from "moment";
import getLanguage from "./MyLanguange";

/**
 * @param {array} locations list location yg ada
 * @param {string} id_location {OPTIONAL} id_location yang ingin dicari
 */
const getLocation = (locations, id_location) => {
  let location_name = <IntlMessages id="All" />;

  if (id_location) {
    locations.map(data =>
      data.id_location === id_location
        ? (location_name = data.location_name)
        : null
    );
  }

  return location_name;
};

const getDepartment = (departments, id_department) => {
  let department_name = <IntlMessages id="All" />;

  if (id_department) {
    departments.map(data =>
      data.id_department === id_department
        ? (department_name = data.department_name)
        : null
    );
  }

  return department_name;
};

const getProject = (projects, id_project) => {
  let project_name = <IntlMessages id="All" />;

  if (id_project) {
    projects.map(data =>
      data.id_project === id_project ? (project_name = data.project_name) : null
    );
  }

  return project_name;
};

const getCashBank = (cash_banks, id_cash_bank) => {
  let cash_bank_name = <IntlMessages id="All" />;

  if (id_cash_bank) {
    cash_banks.map(data =>
      data.id_cash_bank === id_cash_bank
        ? (cash_bank_name =
          data.bank_account_number === null
            ? data.cash_bank_code
            : data.cash_bank_code + " (" + data.bank_account_number + ")")
        : null
    );
  }

  return cash_bank_name;
};

const getTransTypeString = data => {
  let transaction_type_string;

  switch (data) {
    case 1:
      transaction_type_string = getLanguage({ id: "openingBalance" });
      break;
    case 2:
      transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
      break;
    case 3:
      transaction_type_string = getLanguage({ id: "sidebar.issueReceipt" });
      break;
    case 4:
      transaction_type_string = getLanguage({ id: "sidebar.purchaseReceipts" });
      break;
    case 5:
      transaction_type_string = getLanguage({ id: "sidebar.purchaseInvoices" });
      break;
    case 6:
      transaction_type_string = getLanguage({ id: "sidebar.purchaseReturns" });
      break;
    case 7:
      transaction_type_string = getLanguage({ id: "sidebar.itemTransfer" });
      break;
    case 8:
      transaction_type_string = getLanguage({ id: "sidebar.deliveryOrder" });
      break;
    case 9:
      transaction_type_string = getLanguage({ id: "sidebar.salesInvoice" });
      break;
    case 11:
      transaction_type_string = getLanguage({ id: "sidebar.salesReturn" });
      break;
    case 12:
      transaction_type_string = getLanguage({ id: "sidebar.fixedAsset" });
      break;
    case 13:
      transaction_type_string = getLanguage({ id: "journalVoucher" });
      break;
    case 14:
      transaction_type_string = getLanguage({ id: "accountReceivablePayment" });
      break;
    case 15:
      transaction_type_string = getLanguage({ id: "accountPayablePayment" });
      break;
    case 16:
      transaction_type_string = getLanguage({ id: "creditMemo" });
      break;
    case 17:
      transaction_type_string = getLanguage({ id: "debitMemo" });
      break;
    case 18:
      transaction_type_string = getLanguage({ id: "sidebar.cashManagement" });
      break;
    case 19:
      transaction_type_string = getLanguage({ id: "sidebar.jobCosting" });
      break;
    case 20:
      transaction_type_string = getLanguage({ id: "sidebar.jobCostRollover" });
      break;
    case 21:
      transaction_type_string = getLanguage({ id: "supplierDeposit" });
      break;
    case 22:
      transaction_type_string = getLanguage({ id: "customerDeposit" });
      break;
    default:
      transaction_type_string = "-";

  }
  return transaction_type_string
}

/**
 * @param componentRef (Required - Ref) Ref dari component untuk trigger di tombol print
 * @param title (Required - String) Judul dokument yang akan di print
 * @param columns (Required - Array) List column yang akan diprint
 * @param data (Required - Array) List data yang akan diprint TANPA PAGINATION
 * @param table (Optional - Table) Component Tabel custom untuk di print
 * @param scale (Optional - Float) Scale Tabel custom untuk di print
 * @param filter (Required - Object) Filter yang ada di state
 * @param locations (Optional - Array) Diisi array location apabila ingin manampilkan data lokasi di print report
 * @param filterLocation (Optional - String) Diisi string location key apabila ingin custom key id_location
 * @param departments (Optional - Array) Diisi array department apabila ingin manampilkan data lokasi di print report
 * @param filterDepartment (Optional - String) Diisi string department key apabila ingin custom key id_department
 * @param projects (Optional - Array) Diisi array project apabila ingin manampilkan data lokasi di print report
 * @param filterProject (Optional - String) Diisi string project key apabila ingin custom key id_project
 * @param filterDate (Optional - String) Diisi string date key apabila ingin custom key start_date dan end_date
 * @param dateFormat (Optional - String) Diisi string date format moment apabila ingin custom date format
 */
class PrintWrapper extends Component {
  table = () => {
    const {
      summariesColumns,
      summariesData,
      columns,
      data,
      defaultExpandAllRows,
      expandedRowKeys,
      expandedRowRender,
      separateTable,
    } = this.props;
    if (summariesColumns) {
      return (
        <>
          <Divider
            orientation="left"
            style={{
              fontSize: "12px",
              alignItems: "center",
              display: "flex"
            }}
          >
            <IntlMessages id="summaries" style={{ marginTop: "-10" }} />
          </Divider>

          <PrintTable
            columns={summariesColumns}
            dataSource={summariesData}
            pagination={false}
          />

          <Divider
            orientation="left"
            style={{
              fontSize: "12px",
              alignItems: "center",
              display: "flex"
            }}
          >
            <IntlMessages id="details" style={{ marginTop: "-10" }} />
          </Divider>

          <PrintTable columns={columns} dataSource={data} pagination={false} />
        </>
      );
    } else if (defaultExpandAllRows) {
      return (
        <>
          <PrintTable
            columns={columns}
            dataSource={data}
            defaultExpandAllRows={true}
            expandedRowKeys={expandedRowKeys}
            expandedRowRender={expandedRowRender}
            pagination={false}
          />
        </>
      );
    } else if (separateTable) {
      // account history case
      const template = data.map((item, index) => {
        if (item[1]) {
          item[1][0].transaction_type_string = getTransTypeString(item[1][0].transaction_type)
          item.dataSource = []
          var totalCredit = []
          var totalDebit = []
          item[1].map(x => {
            if (x.debit_credit === 1) {
              totalDebit.push(x.amount)
              return item.dataSource.push({
                ...x,
                key: x.id_gl_transaction,
                debit: x.amount,
                credit: null,
              });
            } else {
              totalCredit.push(x.amount)
              return item.dataSource.push({
                ...x,
                key: x.id_gl_transaction,
                debit: null,
                credit: x.amount,
              });
            }
          })
          var debit = ""
          var credit = ""
          if (totalCredit.length > 0) {
            credit = totalCredit.reduce(function (a, b) { return parseFloat(a) + parseFloat(b) })
          }
          if (totalDebit.length > 0) {
            debit = totalDebit.reduce(function (a, b) { return parseFloat(a) + parseFloat(b) })
          }
          item.dataSource.push({
            key: item[1][0].id_gl_transaction + item[1][0].id_source_transaction,
            account_code: "Total",
            debit: parseFloat(debit),
            credit: parseFloat(credit)
          })
        }
        // console.log("data parent")
        return (
          <div key={index}>
            <Row style={{ fontSize: "12px", marginBottom: "5px", textAlign: "left" }}>
              <Col span={4}><strong><IntlMessages id="salesReturns.filter.date"></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={19}>{moment(item[1][0].transaction_date).format("DD MMMM YYYY HH:mm:ss")}</Col>
            </Row>
            <Row style={{ fontSize: "12px", marginBottom: "5px", textAlign: "left" }}>
              <Col span={4}><strong><IntlMessages id="transactionType"></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={19}>{item[1][0].transaction_type_string}</Col>
            </Row>
            <Row style={{ fontSize: "12px", marginBottom: "5px", textAlign: "left" }}>
              <Col span={4}><strong><IntlMessages id="transactionNo"></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={19}>{item[1][0].source_transaction_no}</Col>
            </Row>
            <div style={{ padding: "0 20px 40px" }} >
              <PrintTable columns={columns} dataSource={item.dataSource} pagination={false} />
            </div>
          </div>
        )
      })

      return template
    } else {
      return (
        <>
          <PrintTable             
            columns={columns} 
            dataSource={data} 
            pagination={false} />
        </>
      );
    }
  };
  render() {
    const {
      agingFilter,
      componentRef,
      user,
      title,
      filter,
      locations,
      departments,
      projects,
      cash_banks,
      customFilter,
      filterLocation,
      filterDepartment,
      filterProject,
      filterCashBank,
      filterDate,
      dateFormat,
      children
    } = this.props;
    let { company } = user ? user : [];
    company = company ? company : [];

    const from =
      filter && filter.start_date
        ? filter.start_date instanceof moment
          ? filter.start_date
          : moment(filter.start_date, "YYYY-MM-DD HH:mm:ss")
        : null;

    const to =
      filter && filter.end_date
        ? filter.end_date instanceof moment
          ? filter.end_date
          : moment(filter.end_date, "YYYY-MM-DD HH:mm:ss")
        : null;

    const previous_date =
      filter && filter.first_transaction_date
        ? filter.first_transaction_date instanceof moment
          ? filter.first_transaction_date
          : moment(filter.first_transaction_date, "YYYY-MM-DD HH:mm:ss")
        : null;

    const current_date =
      filter && filter.second_transaction_date
        ? filter.second_transaction_date instanceof moment
          ? filter.second_transaction_date
          : moment(filter.second_transaction_date, "YYYY-MM-DD HH:mm:ss")
        : null;

    const previous_date_range =
      filter && filter.first_start_date 
        ? filter.first_start_date instanceof moment
          ? 
            {
              start_date: filter.first_start_date,
              end_date: filter.first_end_date
            }
          : 
            {
              start_date: moment(filter.first_start_date, "YYYY-MM-DD HH:mm:ss"),
              end_date: moment(filter.first_end_date, "YYYY-MM-DD HH:mm:ss")
            }
        : null;
  
    const current_date_range =
      filter && filter.second_start_date
        ? filter.second_start_date instanceof moment
          ? 
            {
              start_date: filter.second_start_date,
              end_date: filter.second_end_date
            }
          : 
            {
              start_date: moment(filter.second_start_date, "YYYY-MM-DD HH:mm:ss"),
              end_date: moment(filter.second_end_date, "YYYY-MM-DD HH:mm:ss")
            }
        : null;

    const on =
      filter && filter[filterDate]
        ? filter[filterDate] instanceof moment
          ? filter[filterDate].format(`${dateFormat}`)
          : moment(filter[filterDate], "YYYY-MM-DD HH:mm:ss").format(
            `${dateFormat}`
          )
        : null;

    return (
      <div style={{ display: "none" }}>
        <div
          ref={el => componentRef(el)}
          style={{ width: "100%", maxWidth: "100%" }}
        >
          <div 
            className="page-header" 
            // style={{ height: height ? height : "100px" }}
          >
            <Text>{company.company_name}</Text>
            <Title style={{ marginBottom: 0 }}>{title}</Title>
            {from && to ? (
              <Text>
                <IntlMessages
                  id="reportDateRange"
                  values={{
                    fromDate: from.format("DD MMMM YYYY"),
                    toDate: to.format("DD MMMM YYYY")
                  }}
                />
              </Text>
            ) : from ? (
              <Text>
                <IntlMessages
                  id="reportDate"
                  values={{ fromDate: from.format("DD MMMM YYYY") }}
                />
              </Text>
            ) : null}

            { previous_date && current_date 
                ?
                  (
                    <>
                      <IntlMessages id="previousDate"/>
                      <Text>
                        {" : "+ previous_date.format("DD MMMM YYYY HH:mm:ss") + ", "}
                      </Text>
                      <IntlMessages id="currentDate"/>
                      <Text>
                        {" : "+ current_date.format("DD MMMM YYYY HH:mm:ss")}
                      </Text>
                    </>
                  )
                : null            
            }

            { previous_date_range && previous_date_range 
                ?
                  (
                    <>
                      <IntlMessages id="previousDate"/>
                      <Text>
                        {
                          " : " + previous_date_range.start_date.format("DD MMMM YYYY HH:mm:ss") + 
                          " - " + previous_date_range.end_date.format("DD MMMM YYYY HH:mm:ss") + 
                          ", "
                        }
                      </Text>
                      <IntlMessages id="currentDate"/>
                      <Text>
                        {
                          " : " + current_date_range.start_date.format("DD MMMM YYYY HH:mm:ss") +
                          " - " + current_date_range.end_date.format("DD MMMM YYYY HH:mm:ss") 
                        }
                      </Text>
                    </>
                  )
                : null            
            }

            {on ? <Text>{on}</Text> : null}

            {agingFilter ? <Text>{agingFilter}</Text> : null}

            {customFilter && customFilter.length > 0
              ? customFilter.map((data, index) => (
                <React.Fragment>
                  <MiniText>
                    <IntlMessages id={data.intlMessage}>
                      {txt => `${txt} : `}
                    </IntlMessages>
                    {data.value}
                  </MiniText>
                  {index < customFilter.length - 1 ? (
                    <span style={{ marginRight: "15px" }} />
                  ) : null}
                </React.Fragment>
              ))
              : null}

            <Row type="flex">
              <Col span={12}>
                {departments && filter ? (
                  <div style={{ textAlign: "left" }}>
                    <MiniText italic={false} style={{ marginBottom: "1em" }}>
                      <IntlMessages id="sidebar.department">
                        {txt => `${txt} : `}
                      </IntlMessages>
                      {getDepartment(
                        departments,
                        filterDepartment
                          ? filter[filterDepartment]
                          : filter.id_department
                      )}
                    </MiniText>
                  </div>
                ) : null}

                {projects && filter ? (
                  <div style={{ textAlign: "left" }}>
                    <MiniText italic={false} style={{ marginBottom: "1em" }}>
                      <IntlMessages id="sidebar.project">
                        {txt => `${txt} : `}
                      </IntlMessages>
                      {getProject(
                        projects,
                        filterProject
                          ? filter[filterProject]
                          : filter.id_project
                      )}
                    </MiniText>
                  </div>
                ) : null}

                {cash_banks && filter ? (
                  <div style={{ textAlign: "left" }}>
                    <MiniText italic={false} style={{ marginBottom: "1em" }}>
                      <IntlMessages id="sidebar.cashBank">
                        {txt => `${txt} : `}
                      </IntlMessages>
                      {getCashBank(
                        cash_banks,
                        filterCashBank
                          ? filter[filterCashBank]
                          : filter.id_cash_bank
                      )}
                    </MiniText>
                  </div>
                ) : null}

                {filter && filter.operator && filter.current_balance ? (
                  <div style={{ textAlign: "left" }}>
                    <MiniText italic={false} style={{ marginBottom: "1em" }}>
                      <IntlMessages id="currentBalance">
                        {txt => `${txt} : `}
                      </IntlMessages>
                      {filter.operator + " " +
                        parseFloat(filter.current_balance)
                          .toFixed(2)
                          .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                    </MiniText>
                  </div>
                ) : null}

                {filter && filter.stock_age && filter.bank_interest ? (
                  <div style={{ textAlign: "left" }}>
                    <MiniText italic={false} style={{ marginBottom: "1em" }}>
                      <IntlMessages id="stockAgeBankInterest">
                        {txt => `${txt} : `}
                      </IntlMessages>
                      <IntlMessages id="locations.add.leadTime.unit">
                        {txt =>
                          `${
                          filter.stock_age
                          } (${txt})/${filter.bank_interest.toFixed(2)}%`
                        }
                      </IntlMessages>
                    </MiniText>
                  </div>
                ) : null}
              </Col>

              <Col span={12}>
                {locations && filter ? (
                  <div style={{ textAlign: "right" }}>
                    <MiniText italic={false} style={{ marginBottom: "1em" }}>
                      <IntlMessages id="sidebar.location">
                        {txt => `${txt} : `}
                      </IntlMessages>
                      {getLocation(
                        locations,
                        filterLocation
                          ? filter[filterLocation]
                          : filter.id_location
                      )}
                    </MiniText>
                  </div>
                ) : null}
              </Col>
            </Row>
          </div>

          <div className="page-footer">
            <div style={{ width: "100%" }}>
              <Divider style={{ margin: "0 auto 0.5em auto" }} />
            </div>

            <MiniText style={{ marginBottom: "1em" }}>
              {siteConfig.footerReport}
            </MiniText>

            <br />

            <MiniText>
              <IntlMessages
                id="printedOn"
                values={{
                  dateTime: moment().format("HH:mm - DD MMMM YYYY")
                }}
              >
                {txt => `${txt}`}
              </IntlMessages>
            </MiniText>

            {/* <Row type="flex" justify="space-between">
              <Col>
                <MiniText>
                  <IntlMessages
                    id="printedOn"
                    values={{
                      dateTime: moment().format("DD MMMM YYYY - HH:mm")
                    }}
                  >
                    {txt => `${txt}`}
                  </IntlMessages>
                </MiniText>
              </Col>
              <Col>
                <MiniText>
                  <span id="#current-page">
                    <IntlMessages id="page">{txt => `${txt}  `}</IntlMessages>
                  </span>
                  <span id="#total-page">
                    <IntlMessages id="from">{txt => `  ${txt}`}</IntlMessages>
                  </span>
                </MiniText>
              </Col>
            </Row> */}
          </div>

          <table className="table-content">
            <thead className="thead-header">
              <tr>
                <td>
                  {/* <!--place holder for the fixed-position header--> */}
                  <div className="page-header-space" />
                </td>
              </tr>
            </thead>

            <tbody>
              <tr>
                <td style={{ textAlign: "center" }}>
                  {/* <!--*** CONTENT GOES HERE ***--> */}
                  <Row>
                    {children ? children : <Col span={24}>{this.table()}</Col>}
                  </Row>
                </td>
              </tr>
            </tbody>

            <tfoot className="tfoot-footer">
              <tr>
                <td>
                  {/* <!--place holder for the fixed-position footer--> */}
                  <div className="page-footer-space" />
                </td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return { user: state.auth.user };
};

export default compose(connect(mapStateToProps))(PrintWrapper);
