const actions = {
  getAccountHistorySubLedgerProjectReports: (page, search, filter) => ({
    type: "GET_ACCOUNT_HISTORY_SUB_LEDGER_PROJECT_REPORTS_REQUEST",
    payload: {
      search: search,
      filter: filter
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
